<template>
  <div class="panorama-container" ref="panoramaContainer" @mousedown="startDrag" @mouseup="endDrag" @mousemove="onDrag" @mouseleave="endDrag">
    <div class="panorama" :style="{ transform: `translateX(${position.x}px)` }">
      <!-- 渲染多个场景图片 -->
      <div class="scenes-container">
        <div 
          v-for="scene in scenes" 
          :key="scene.id"
          class="scene-image-wrapper"
          @click="onSceneClick(scene)"
          :class="{ 'active': currentScene === scene.id, 'locked': !scene.unlocked }"
        >
          <img 
            :src="scene.imageUrl" 
            alt="{{ scene.name }}"
            class="scene-image" 
            @mousedown.stop="startDrag"
          >
          <div class="scene-overlay">
            <h3>{{ scene.name }}</h3>
            <span v-if="!scene.unlocked" class="lock-overlay">🔒</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 拖动提示 -->
    <div v-if="showDragHint" class="drag-hint">
      <p>按住鼠标拖动查看全景</p>
      <p>点击已解锁场景进入</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Panorama',
  props: {
    scenes: {
      type: Array,
      required: true
    },
    currentScene: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      position: { x: 0 },
      isDragging: false,
      startX: 0,
      startPosition: { x: 0 },
      maxWidth: 0,
      showDragHint: true,
      imageLoadedCount: 0
    }
  },
  mounted() {
    // 监听窗口大小变化
    window.addEventListener('resize', this.calculateMaxWidth);
    
    // 5秒后隐藏拖动提示
    setTimeout(() => {
      this.showDragHint = false;
    }, 5000);
    
    // 等待所有图片加载完成后计算最大宽度
    this.checkImagesLoaded();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.calculateMaxWidth);
  },
  methods: {
    checkImagesLoaded() {
      // 等待DOM更新后检查图片
      this.$nextTick(() => {
        const images = this.$refs.panoramaContainer?.querySelectorAll('.scene-image');
        if (!images || images.length === 0) {
          // 如果没有图片，稍后再试
          setTimeout(this.checkImagesLoaded, 100);
          return;
        }
        
        images.forEach(img => {
          if (img.complete) {
            this.imageLoadedCount++;
          } else {
            img.onload = () => {
              this.imageLoadedCount++;
              if (this.imageLoadedCount === images.length) {
                this.calculateMaxWidth();
              }
            };
          }
        });
        
        // 如果所有图片已经加载完成
        if (this.imageLoadedCount === images.length) {
          this.calculateMaxWidth();
        }
      });
    },
    calculateMaxWidth() {
      const containerWidth = this.$refs.panoramaContainer?.clientWidth || 0;
      const scenesContainer = this.$refs.panoramaContainer?.querySelector('.scenes-container');
      const totalWidth = scenesContainer ? scenesContainer.scrollWidth : 0;
      this.maxWidth = Math.max(0, totalWidth - containerWidth);
    },
    startDrag(event) {
      this.isDragging = true;
      this.startX = event.clientX;
      this.startPosition.x = this.position.x;
      // 确保设置了正确的鼠标样式
      document.body.style.cursor = 'grabbing';
      if (this.$refs.panoramaContainer) {
        this.$refs.panoramaContainer.style.cursor = 'grabbing';
      }
    },
    endDrag() {
      this.isDragging = false;
      document.body.style.cursor = 'default';
      if (this.$refs.panoramaContainer) {
        this.$refs.panoramaContainer.style.cursor = 'grab';
      }
    },
    onDrag(event) {
      if (!this.isDragging) return;
      
      const deltaX = event.clientX - this.startX;
      let newX = this.startPosition.x + deltaX;
      
      // 限制拖动范围
      newX = Math.max(-this.maxWidth, Math.min(0, newX));
      
      this.position.x = newX;
    },
    onSceneClick(scene) {
      // 只有在非拖动状态下才响应点击
      if (this.isDragging) return;
      
      if (scene.unlocked) {
        // 通知父组件场景被点击
        this.$emit('sceneClick', scene.id);
      } else {
        // 可以添加解锁提示
        alert(`需要达到${scene.unlockScore}分才能解锁${scene.name}！`);
      }
    },
    scrollToScene(sceneId) {
      const scene = this.scenes.find(s => s.id === sceneId);
      if (!scene || !this.$refs.panoramaContainer) return;
      
      const containerWidth = this.$refs.panoramaContainer.clientWidth;
      const scenesContainer = this.$refs.panoramaContainer.querySelector('.scenes-container');
      const sceneElement = this.$refs.panoramaContainer.querySelector(`.scene-image-wrapper:nth-child(${this.scenes.findIndex(s => s.id === sceneId) + 1})`);
      
      if (!scenesContainer || !sceneElement) return;
      
      // 计算目标场景中心位置
      const sceneRect = sceneElement.getBoundingClientRect();
      const containerRect = this.$refs.panoramaContainer.getBoundingClientRect();
      const targetPosition = -(sceneRect.left - containerRect.left - (containerWidth - sceneRect.width) / 2);
      
      // 限制在有效范围内
      const totalWidth = scenesContainer.scrollWidth;
      const maxScroll = Math.max(0, totalWidth - containerWidth);
      const targetX = Math.max(-maxScroll, Math.min(0, targetPosition));
      
      // 平滑滚动动画
      this.smoothScrollTo(targetX);
    },
    smoothScrollTo(targetX) {
      const duration = 500; // 动画持续时间
      const startX = this.position.x;
      const distance = targetX - startX;
      const startTime = performance.now();
      
      const animateScroll = (currentTime) => {
        const elapsed = currentTime - startTime;
        const progress = Math.min(elapsed / duration, 1);
        // 使用缓动函数使动画更自然
        const easeOutQuad = progress * (2 - progress);
        
        this.position.x = startX + distance * easeOutQuad;
        
        if (progress < 1) {
          requestAnimationFrame(animateScroll);
        }
      };
      
      requestAnimationFrame(animateScroll);
    }
  }
}
</script>

<style scoped>
.panorama-container {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  cursor: grab;
  user-select: none; /* 防止拖动时选中文字 */
}

.panorama {
  position: relative;
  height: 100%;
  transition: transform 0s; /* 拖动时无过渡动画 */
}

.scenes-container {
  display: flex;
  height: 100%;
}

.scene-image-wrapper {
  height: 100%;
  min-width: 100%;
  position: relative;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.scene-image-wrapper:hover:not(.locked) {
  transform: scale(1.02);
  z-index: 10;
}

.scene-image-wrapper.locked {
  opacity: 0.5;
  cursor: not-allowed;
}

.scene-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
  user-select: none;
}

.scene-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0,0,0,0.8), transparent);
  color: white;
  padding: 20px;
  text-align: center;
}

.scene-overlay h3 {
  margin: 0;
  font-size: 2rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.8);
}

.lock-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 5rem;
  opacity: 0.7;
}

.drag-hint {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 15px 25px;
  border-radius: 25px;
  text-align: center;
  backdrop-filter: blur(5px);
  animation: fadeInOut 5s ease-in-out;
}

.drag-hint p {
  margin: 5px 0;
  font-size: 14px;
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}
</style>